<template>
  <el-dialog
    :title="!dataForm.id ? '新增' : '修改'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    width="100%"
    :fullscreen="true"
    center>
    <div style="height: 100%">
      <iframe src="http://localhost:8098/fast/act/goActiviti" id="mobsf" scrolling="no" frameborder="0" style="width: 100%;height: 800px;"></iframe>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>
<script>
  export default {
    data () {
      return {
        visible: false,
        dataForm: {
          id: '',
          category: '',
          createTime: '',
          deploymentId: '',
          haseditorsource: '',
          key: '',
          lastUpdateTime: '',
          name: '',
          version: ''
        }
      }
    },
    methods: {
      init (id) {
        this.dataForm.id = id || ''
        this.visible = true
      }
    }
  }
</script>



